

















import { Vue, Component, Prop } from 'vue-property-decorator'
import { ICardModel } from 'app/objects/card'

@Component
export default class CardGallery extends Vue {
    @Prop() cards!: ICardModel[];

    state = {
        shown: 0,
    };

    show(idx: number) {
        this.state.shown = idx;
    }
}
